.switches-container{
  display: flex;
  padding: 0.3rem 0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .3),
  0 -2px 4px 0 rgba(0, 0, 0, .2);
  border-radius: .5rem;
  position: relative;
  z-index: 3;
}

.switch-name {
  margin-bottom: .5rem;
  color: #FFFFFF;
}

.power-section{
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.volume-section{
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-content: space-between;
}

.kit-section{
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

/* switch */
.switch{
    position: relative;
    display: inline-block;
    width: 60px;
    height: 30px;
  }

  .switch input {
    display: none;
  }

input:checked + .switch-slider {
      opacity: 1;
  }

input:checked + .switch-slider:before {
  transform: translateX(24px);
}

  .switch span{
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: transparent;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .3),
    0 -2px 4px 0 rgba(0, 0, 0, .2);
    transition: .4s;
    border-radius: 30px;
    z-index: 5;
  }

  .switch-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #FFFFFF;
    transition: .4s;
    border-radius: 30px;
    z-index: 6;
  }

  .switch-slider .always-active {
      opacity: 1;
  }

  .switch-slider:before {
    position: absolute;
    content: "";
    height: 24px;
    width: 24px;
    left: 7px;
    bottom: 3px;
    background-color: #FFFFFF;
    opacity: 100%;
    transition: .4s;
    border-radius: 50%;
    z-index: 2;
  }

  /* Slider */
  .slider {
    -webkit-appearance: none;
    width: 100%;
    height: 30px;
    border-radius: 30px;
    background-color: transparent;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .3),
    0 -2px 4px 0 rgba(0, 0, 0, .2);
    outline: none;
  }

  .slider:hover {
    opacity: 1;
  }

  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 24px;
    height: 24px;
    left:3px;
    bottom: 3px;
    border-radius: 50%;
    background-color: #FFFFFF;
    opacity: 100%;
    cursor: pointer;
  }


